import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { StudentRouterModule } from './student.router';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule } from '@angular/forms';

import { StudentListComponent } from './components/student-list/student-list.component';
import { StudentProfileFirstComponent } from './components/student-profile-first/student-profile-first.component';
import { StudentProfileSecondComponent } from './components/student-profile-second/student-profile-second.component';
import { StudentProfileThirdComponent } from './components/student-profile-third/student-profile-third.component';
import { StudentProfileFourthComponent } from './components/student-profile-fourth/student-profile-fourth.component'

import {
  MatAutocompleteModule,
  MatButtonModule,
  MatButtonToggleModule,
  MatCardModule,
  MatCheckboxModule,
  MatChipsModule,
  MatDatepickerModule,
  MatDialogModule,
  MatExpansionModule,
  MatGridListModule,
  MatIconModule,
  MatInputModule,
  MatListModule,
  MatMenuModule,
  MatNativeDateModule,
  MatPaginatorModule,
  MatProgressBarModule,
  MatProgressSpinnerModule,
  MatRadioModule,
  MatRippleModule,
  MatSelectModule,
  MatSidenavModule,
  MatSliderModule,
  MatSlideToggleModule,
  MatSnackBarModule,
  MatSortModule,
  MatTableModule,
  MatTabsModule,
  MatToolbarModule,
  MatTooltipModule,
  MatStepperModule,
} from '@angular/material';
import {HttpModule} from '@angular/http';
import {CdkTableModule} from '@angular/cdk/table';


@NgModule({
  exports: [
    CdkTableModule,
    MatAutocompleteModule,
    MatButtonModule,
    MatButtonToggleModule,
    MatCardModule,
    MatCheckboxModule,
    MatChipsModule,
    MatStepperModule,
    MatDatepickerModule,
    MatDialogModule,
    MatExpansionModule,
    MatGridListModule,
    MatIconModule,
    MatInputModule,
    MatListModule,
    MatMenuModule,
    MatNativeDateModule,
    MatPaginatorModule,
    MatProgressBarModule,
    MatProgressSpinnerModule,
    MatRadioModule,
    MatRippleModule,
    MatSelectModule,
    MatSidenavModule,
    MatSliderModule,
    MatSlideToggleModule,
    MatSnackBarModule,
    MatSortModule,
    MatTableModule,
    MatTabsModule,
    MatToolbarModule,
    MatTooltipModule,
  ]
})
export class SliderMaterialModule {}

















@NgModule({
  imports: [
    CommonModule,
    StudentRouterModule,
    RouterModule,
    FormsModule,
    SliderMaterialModule
  ],
  declarations: [
    StudentListComponent,
    StudentProfileFirstComponent,
    StudentProfileSecondComponent,
    StudentProfileThirdComponent,
    StudentProfileFourthComponent
  ]
})
export class StudentModule { }



